fcDemos['ex25'] = {
  title: 'Lifecycle events',
  description:
    'This sample lists the basic lifecycle events at the time of rendering FusionCharts',
  code:
    "import React, { Component } from \"react\";\nimport FusionCharts from 'react-native-fusioncharts';\nimport { View, Text, StyleSheet, Platform } from 'react-native';\n\nexport default class LifeCycleEvents extends Component {\n  constructor(props) {\n    super(props);\n    this.state = {\n      type: 'column2d',\n      width: '100%',\n      height: '100%',\n      dataFormat: 'json',\n      displayValue: 'Status: ',\n      dataSource: {/* see data tab */},\n      events: {\n        beforeDataUpdate: () => {\n          const displayValue = this.state.displayValue + ' beforeDataUpdate';\n          this.setState({\n            displayValue\n          });\n        },\n        dataUpdated: () => {\n          const displayValue = this.state.displayValue + ', dataUpdated';\n          this.setState({\n            displayValue\n          });\n        },\n        drawComplete: () => {\n          const displayValue = this.state.displayValue + ', drawComplete';\n          this.setState({\n            displayValue\n          });\n        },\n        renderComplete: () => {\n          const displayValue = this.state.displayValue + ', renderComplete';\n          this.setState({\n            displayValue\n          });\n        }\n      }\n    }\n    this.libraryPath = Platform.select({\n      android: { uri: 'file:///android_asset/fusioncharts.html' },\n      ios: require('./assets/fusioncharts.html')\n    })\n  }\n  render() {\n    return (\n      <View style={styles.container}>\n        <Text style={styles.header}>Special Events</Text>\n        <View style={styles.chartContainer}>\n          <FusionCharts\n            type={this.state.type}\n            width={this.state.width}\n            height={this.state.height}\n            dataFormat={this.state.dataFormat}\n            dataSource={this.state.dataSource}\n            events={this.state.events}\n            libraryPath={this.libraryPath} // set the libraryPath property\n          />\n        </View>\n        <Text style={styles.header}>{this.state.displayValue}</Text>\n      </View>\n    )\n  }\n}\n\nconst styles = StyleSheet.create({\n  container: {\n    flex: 1,\n    padding: 10,\n    backgroundColor: 'white'\n  },\n  header: {\n    fontWeight: 'bold',\n    fontSize: 20,\n    textAlign: 'center',\n    paddingBottom: 16\n  },\n  chartContainer: {\n    height: 400,\n    borderColor: '#000',\n    borderWidth: 2\n  }\n});\n"
};

