fcDemos['ex29'] = {
  title: 'Usage of FusionCharts methods (API)',
  description:
    'Change a chart attribute by calling FusionCharts setChartAttribute API',
  code: `import React, { Component } from 'react';\nimport FusionCharts from 'react-native-fusioncharts';\nimport { View, Text, StyleSheet, Platform, Button } from 'react-native';\n\nexport default class ChartAttributeApi extends Component {\n  constructor(props) {\n    super(props);\n    this.apiCaller = null;\n\n    this.state = {\n      type: 'column2d',\n      width: '100%',\n      height: '100%',\n      dataFormat: 'json',\n      dataSource: {/* see data tab */},\n    }\n    this.libraryPath = Platform.select({\n      android: { uri: 'file:///android_asset/fusioncharts.html' },\n      ios: require('./assets/fusioncharts.html')\n    });\n\n    this.changeCaption = this.changeCaption.bind(this);\n    this.changeYAxis = this.changeYAxis.bind(this);\n    this.changeXAxis = this.changeXAxis.bind(this);\n    this.bindApiCaller = this.bindApiCaller.bind(this);\n  }\n\n  bindApiCaller(caller) {\n    this.apiCaller = caller;\n  }\n\n  changeCaption() {\n    this.apiCaller(\`window.chartObj.setChartAttribute('caption', 'Caption');\`);\n  }\n\n  changeYAxis() {\n    this.apiCaller(\n      \`window.chartObj.setChartAttribute('xAxisName', 'X-Axis Name')\`\n    );\n  }\n\n  changeXAxis() {\n    this.apiCaller(\n      \`window.chartObj.setChartAttribute('yAxisName', 'Y-Axis Name')\`\n    );\n  }\n\n  render() {\n    return (\n      <View style={styles.container}>\n        <Text style={styles.header}>Annotations</Text>\n        <View style={styles.chartContainer}>\n          <FusionCharts\n            type={this.state.type}\n            width={this.state.width}\n            height={this.state.height}\n            dataFormat={this.state.dataFormat}\n            dataSource={this.state.dataSource}\n            onInitialized={caller => {\n              this.bindApiCaller(caller);\n            }}\n            libraryPath={this.libraryPath} // set the libraryPath property\n          />\n        </View>\n        <View style={styles.btns}>\n          <Button\n            onPress={() => this.changeCaption()}\n            title="CHANGE CAPTION TO: CAPTION"\n            color="#841584"\n          />\n          <Button\n            onPress={() => this.changeXAxis()}\n            title="CHANGE X_AXIS NAME TO: X-AXIS NAME"\n            color="#841584"\n          />\n          <Button\n            onPress={() => this.changeYAxis()}\n            title="CHANGE Y_AXIS NAME TO: Y-AXIS NAME"\n            color="#841584"\n          />\n        </View>\n      </View>\n    )\n  }\n}\n\nconst styles = StyleSheet.create({\n  container: {\n    flex: 1,\n    padding: 10,\n    backgroundColor: 'white'\n  },\n  header: {\n    fontWeight: 'bold',\n    fontSize: 20,\n    textAlign: 'center',\n    paddingBottom: 16\n  },\n  chartContainer: {\n    height: 400,\n    borderColor: '#000',\n    borderWidth: 2\n  },\n  btns: {\n    display: 'flex',\n    justifyContent: 'center'\n  }\n});\n`
};

